import React, { useState, useContext } from 'react';
import AppContext from '../Context/AppContext';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const inputStyle = {
  borderBottom: '1px solid #f79007',
  borderTop: 'none',
  paddingTop: '10px',
  paddingLeft: '20px',
};

const formTextAlign = {
  paddingBottom: '30px',
  textAlign: 'left',
};

function Tab2() {
  const {setKey, setTokenData} = useContext(AppContext);
  const [input, setInput] = useState({});

  const handleChange = ({ target }) => {
    setInput({...input, [target.name]: target.value });
  }
    
  const backPage = () => {
    setKey('token');
  };

  const submit = async () => {
    setTokenData(input);
    setKey('payment');
  };

  return (
    <Tab>
      <Form.Group as={Row} className="text-center md-4" style={{paddingTop: '20px'}}>
        <Form.Label column="true" md={5} htmlFor="name" style={inputLabel}>Token Name</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="text"
            placeholder="Token name*"
            id="name"
            name="name"
            label="Token name"
            onChange={ handleChange }
          />
            <Form.Text id="nameHelpBlock" style={formTextAlign}>
              The name of the token, 3-25 symbols. Alphanumerical characters, space, and hyphen are accepted.
            </Form.Text>
        </Col>

        <Form.Label column="true" md={5} htmlFor="symbol" style={inputLabel}>Token Symbol</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="text"
            placeholder="Token symbol*"
            id="symbol"
            name="symbol"
            onChange={ handleChange }
          />
            <Form.Text style={formTextAlign}>3-4 characters (example ETH, BTC, BAT, etc.). No spaces. Only alphanumerical characters.</Form.Text>
        </Col>
        
        <Form.Label column="true" md={5} htmlFor="supply" style={inputLabel}>Total Supply</Form.Label>
        <Col md={6}>
          <Form.Control
            style={inputStyle}
            plaintext
            type="number"
            placeholder="Total supply*"
            id="supply"
            name="supply"
            min={1}
            max={1000000000000000}
            step={1}
            onChange={ handleChange }
          />
            <Form.Text style={formTextAlign}>Total amount of tokens to be generated. Minimum value is 1, and maximum maximum 1000000000000000000.</Form.Text>
        </Col>
      
        <Col md={{ span: 0, offset: 2 }} className="text-center mt-4 mb-4">
          <Button variant="danger" column="true" type="button" onClick={ backPage }>Back</Button>
        </Col>
        <Col md={{ span: 6, offset: 3 }} className="text-center mt-4 mb-4">
          <Button variant="info" column="true" type="button" onClick={ submit }>Next</Button>
        </Col>
      </Form.Group>
    </Tab>
  )
}

export default Tab2;
