import React, { useContext, useEffect, useState } from 'react';
import { createToken, getPayload } from '../Services/api';
import { utils } from 'web3';
import AppContext from '../Context/AppContext';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form'
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';

import Tab from './Tab'

const inputLabel = {
  paddingBottom: '20px',
  paddingTop: '15px',
};

const formGroupStyle = {
  display: 'flex',
  alignItems: 'flex-end',
  justifyContent: 'center',
};

function Tab3() {
  const {
    setKey,
    ethereum,
    accounts,
    tokenData,
    setTokenData,
    setJwt,
    jwt,
  } = useContext(AppContext);

  const [payment, setPayment] = useState(null);

  useEffect(() => {
    try {
      const { payment: p } = getPayload(jwt);
      setPayment(p);
    } catch {
      setPayment(null);
    }
  }, [jwt]);

  const handleChange = ({ target }) => {
    setTokenData({ ...tokenData, owner: target.value });
  }

  const backPage = () => {
    setKey('attributes');
  };

  const submit = async () => {
    try{
      const { data: { token } } = await createToken(tokenData);
      setJwt(token);
    } catch (err) {
      alert(err.message);
    };
  };

  const confirmPayment = async () => {
    const value = utils.toWei(payment.amount);
    ethereum
      .request({
        method: 'eth_sendTransaction',
        params: [
          {
            from: tokenData.owner,
            to: payment.address,
            value: utils.toHex(value),
          },
        ],
      })
      .then((hash) => {
        setTokenData({ ...tokenData, hash });
        setKey('confirmation');
      })
      .catch((error) => console.error(error));
  };

  return (
    <Tab>
      <Form.Group
        as={Row}
        className="text-center"
        style={{ paddingTop: '20px' }}
      >
        <Col className="text-center" md={12} >
          <Form.Group as={Col} style={formGroupStyle}>
            <div style={{ width: '60%' }}>
              <Form.Label
                column="true"
                htmlFor="wallet_select"
                style={inputLabel}>
                  Wallet to deploy
              </Form.Label>
              <Form.Control
                as="select"
                id="wallet_select"
                defaultValue="Choose Wallet"
                onChange={ handleChange }
              >
                <option selected value="">Select wallet...</option>
                { accounts.map((acc, i) => <option key={i}>{ acc }</option>) }
              </Form.Control>
            </div>
            <div style={{ width: '20%' }}>
              <Button
                variant="info"
                column="true"
                type="button"
                onClick={ submit }>
                  Generate Payment
              </Button>
            </div>
          </Form.Group>
        </Col>
      </Form.Group>
      {
        payment ?
          <Form.Group
            as={Row}
            className="text-center"
            style={{ paddingTop: '20px' }}
          >
            <Col className="text-center" md={12} >
                <p>
                  O valor total para a criação do seu token é: <span>{`${payment.amount} eth`}</span>.
                  Clique no botão abaixo para confirmar a transação
                </p>
                <Button
                  variant="success"
                  column="true"
                  type="button"
                  onClick={confirmPayment}>
                    Confirm Payment
                </Button>
            </Col>
          </Form.Group> : ''
      }
      <Form.Group as={Row} controlId="formHorizontal" className="text-center mt-4 mb-4">
        <Col sm={{ span: 0, offset: 2 }} className="text-center mt-4 mb-4">
          <Button variant="danger" column="true" type="button" onClick={ backPage }>Back</Button>
        </Col>
        <Col sm={{ span: 6, offset: 3 }} className="text-center mt-4 mb-4">
          <Button variant="info" column="true" type="button" onClick={ submit }>Next</Button>
        </Col>
      </Form.Group>
    </Tab>
  )
}

export default Tab3